import React from "react";
import styled from "styled-components";
// Assets
import AffordableIcon from "../../assets/svg/Services/AffordableIcon";
import RapidIcon from "../../assets/svg/Services/RapidIcon";
import CoolingIcon from "../../assets/svg/Services/CoolingIcon";
import LightweightIcon from "../../assets/svg/Services/LightweightIcon";

export default function ServiceBox({icon, title, subtitle}) {
  let getIcon;

  switch (icon) {
    case "affordable":
      getIcon = <AffordableIcon />;
      break;
    case "rapid":
      getIcon = <RapidIcon />;
      break;
    case "browser":
      getIcon = <CoolingIcon />;
      break;
    case "lightweight":
      getIcon = <LightweightIcon />;
      break;
    default:
      getIcon = <AffordableIcon />;
      break;
  }


  return (
    <Wrapper className="flex flexColumn">
      <IconStyle>{getIcon}</IconStyle>
      <TitleStyle className="font20 extraBold">{title}</TitleStyle>
      <SubtitleStyle className="font13">{subtitle}</SubtitleStyle>
    </Wrapper>
  );
}

const Wrapper = styled.div`
  width: 100%;
`;
const IconStyle = styled.div`
  @media (max-width: 860px) {
    margin: 0 auto;
  }
`;
const TitleStyle = styled.h2`
  width: 100%;
  max-width: 300px;
  margin: 0 auto;
  padding: 40px 0;
  @media (max-width: 860px) {
    padding: 20px 0;
  }
`;
const SubtitleStyle = styled.p`
  width: 100%;
  max-width: 300px;
  margin: 0 auto;
`;