import React from "react";
import styled from "styled-components";
// Components
import ClientSlider from "../Elements/ClientSlider";
import ServiceBox from "../Elements/ServiceBox";
import FullButton from "../Buttons/FullButton";
// Assets
// import AddImage1 from "../../assets/img/add/1_.png";
// import AddImage2 from "../../assets/img/add/2_.png";
// import AddImage3 from "../../assets/img/add/3_.png";
// import AddImage4 from "../../assets/img/add/4_.png";

export default function Services() {
  return (
    <Wrapper id="features">
      <div className="lightBg" style={{ padding: "50px 0" }}>
        <div className="container">
          <ClientSlider />
        </div>
      </div>
      <div className="whiteBg" style={{ padding: "60px 0" }}>
        <div className="container">
          <HeaderInfo>
            <h1 className="font40 extraBold">Features Of Kavach</h1>
            <p className="font13">
              Introducing our latest product with cutting-edge features that will revolutionize your experience.
              <br />
              Get ready to enjoy enhanced functionality and convenience like never before.
            </p>
          </HeaderInfo>
          <ServiceBoxRow className="flex">
            <ServiceBoxWrapper>
              <ServiceBox
                icon="affordable"
                title="Affordable Pricing"
                subtitle="With a price range of just ₹600 to ₹1500, we have made it our mission to ensure that staying cool doesn't have to be expensive. We believe that everyone deserves relief from the heat, without stretching their budget. Whether you're on a tight budget or looking for a cost-effective cooling solution, Kavach provides exceptional value for money. Experience the joy of affordable cooling with Kavach, making comfort accessible to all."
              />
            </ServiceBoxWrapper>
            <ServiceBoxWrapper>
              <ServiceBox
                icon="rapid"
                title="Rapid Full Body Cooling"
                subtitle="Experience the ultimate cooling power with Kavach! In just 3 seconds, this pocket air conditioner delivers rapid full-body cooling, reducing the temperature by up to 33°F. Stay refreshed and comfortable wherever you are, as Kavach's advanced cooling system provides instant relief from the heat. Don't let the sweltering weather slow you down - stay cool with Kavach's remarkable cooling capabilities.."
              />
            </ServiceBoxWrapper>
            <ServiceBoxWrapper>
              <ServiceBox
                icon="browser"
                title="Waterless Cooling"
                subtitle="Stay cool without the hassle of water or ice with Kavach's innovative Waterless Cooling Technology. Designed to provide convenience and efficiency, this cutting-edge feature eliminates the need for refilling or maintenance. Simply activate Kavach, and experience refreshing coolness anytime, anywhere. Whether you're on the go or relaxing at home, enjoy the benefits of waterless cooling and stay comfortably cool with Kavach."
              />
            </ServiceBoxWrapper>
            <ServiceBoxWrapper>
              <ServiceBox icon="lightweight" title="Compact And Lightweight" subtitle="Experience ultimate portability with Kavach's compact and lightweight design. Take it anywhere with ease, whether you're traveling, working, or enjoying outdoor activities. Stay cool and comfortable on the go, as Kavach effortlessly accompanies you, providing cooling relief whenever you need it." />
            </ServiceBoxWrapper>
          </ServiceBoxRow>
        </div>
        <div className="lightBg">
          <div className="container">
            <Advertising className="flexSpaceCenter">
              <AddLeft>
                <h4 className="font15 semiBold">Kavach is useful even in cold weather</h4>
                <h2 className="font40 extraBold">Your Year-Round Comfort Companion!</h2>
                <p className="font12">
                Kavach is the the ultimate pocket air conditioner that keeps you cool in summer and warm in winter. Experience rapid full-body cooling in 3 seconds, with the ability to lower the temperature by up to 33°F during scorching summers. But that's not all – Kavach also provides soothing warmth during chilly winter months. With its portable design, it's the perfect on-the-go companion for any season. Embrace year-round comfort with Kavach today!
                </p>
                <ButtonsRow className="flexNullCenter" style={{ margin: "30px 0"}}>
                  <div style={{ width: "190px" }}>
                    <FullButton title="Pre Order" action={() => alert("clicked")} />
                  </div>
                  <div style={{ width: "190px", marginLeft: "15px" }}>
                    <FullButton title="Contact Us" action={() => { window.location.href="mailto:think.handels@gmail.com" }} border />
                  </div>
                </ButtonsRow>
              </AddLeft>
              <AddRight>
                <AddRightInner>
                  <div className="flexNullCenter">
                    <AddImgWrapp1 className="flexCenter">
                      <img src="https://i.postimg.cc/3r94Qn8L/1-8841528ef84225af607f.png" alt="office" />
                    </AddImgWrapp1>
                    <AddImgWrapp2>
                      <img src="https://i.postimg.cc/3ryX1sCX/2.png" alt="office" />
                    </AddImgWrapp2>
                  </div>
                  <div className="flexNullCenter">
                    <AddImgWrapp3>
                      <img src="https://i.postimg.cc/t7QCNZdM/3.png" alt="office" />
                    </AddImgWrapp3>
                    <AddImgWrapp4>
                      <img src="https://i.postimg.cc/nrQhmYmm/4.png" alt="office" />
                    </AddImgWrapp4>
                  </div>
                </AddRightInner>
              </AddRight>
            </Advertising>
          </div>
        </div>
      </div>
    </Wrapper>
  );
}

const Wrapper = styled.section`
  width: 100%;
`;
const ServiceBoxRow = styled.div`
  @media (max-width: 860px) {
    flex-direction: column;
  }
`;
const ServiceBoxWrapper = styled.div`
  width: 20%;
  margin-right: 5%;
  padding: 80px 0;
  @media (max-width: 860px) {
    width: 100%;
    text-align: center;
    padding: 40px 0;
  }
`;
const HeaderInfo = styled.div`
  @media (max-width: 860px) {
    text-align: center;
  }
`;
const Advertising = styled.div`
  margin: 80px 0;
  padding: 100px 0;
  position: relative;
  @media (max-width: 1160px) {
    padding: 100px 0 40px 0;
  }
  @media (max-width: 860px) {
    flex-direction: column;
    padding: 0 0 30px 0;
    margin: 80px 0 0px 0;
  }
`;
const ButtonsRow = styled.div`
  @media (max-width: 860px) {
    justify-content: space-between;
  }
`;
const AddLeft = styled.div`
  width: 50%;
  p {
    max-width: 475px;
  }
  @media (max-width: 860px) {
    width: 80%;
    order: 2;
    text-align: center;
    h2 {
      line-height: 3rem;
      margin: 15px 0;
    }
    p {
      margin: 0 auto;
    }
  }
`;
const AddRight = styled.div`
  width: 50%;
  position: absolute;
  top: -70px;
  right: 0;
  @media (max-width: 860px) {
    width: 80%;
    position: relative;
    order: 1;
    top: -40px;
  }
`;
const AddRightInner = styled.div`
  width: 100%;
`;
const AddImgWrapp1 = styled.div`
  width: 48%;
  margin: 0 6% 10px 6%;
  img {
    width: 100%;
    height: auto;
    border-radius: 1rem;
    box-shadow: 0 2px 15px rgba(0, 0, 0, 0.3);
    -webkit-box-shadow: 0 2px 15px rgba(0, 0, 0, 0.3);
    -moz-box-shadow: 0 2px 15px rgba(0, 0, 0, 0.3);
  }
`;
const AddImgWrapp2 = styled.div`
  width: 30%;
  margin: 0 5% 10px 5%;
  img {
    width: 100%;
    height: auto;
    border-radius: 1rem;
    box-shadow: 0 2px 15px rgba(0, 0, 0, 0.3);
    -webkit-box-shadow: 0 2px 15px rgba(0, 0, 0, 0.3);
    -moz-box-shadow: 0 2px 15px rgba(0, 0, 0, 0.3);
  }
`;
const AddImgWrapp3 = styled.div`
  width: 20%;
  margin-left: 40%;
  img {
    width: 100%;
    height: auto;
    border-radius: 1rem;
    box-shadow: 0 2px 15px rgba(0, 0, 0, 0.3);
    -webkit-box-shadow: 0 2px 15px rgba(0, 0, 0, 0.3);
    -moz-box-shadow: 0 2px 15px rgba(0, 0, 0, 0.3);
  }
`;
const AddImgWrapp4 = styled.div`
  width: 30%;
  margin: 0 5%auto;
  img {
    width: 100%;
    height: auto;
    border-radius: 1rem;
    box-shadow: 0 2px 15px rgba(0, 0, 0, 0.3);
    -webkit-box-shadow: 0 2px 15px rgba(0, 0, 0, 0.3);
    -moz-box-shadow: 0 2px 15px rgba(0, 0, 0, 0.3);
  }
`;