import React from "react";
import styled from "styled-components";
// Components
import PricingTable from "../Elements/PricingTable";
import FullButton from "../Buttons/FullButton";
// Assets
// import AddImage2 from "../../assets/img/add/kavach_app.png";

export default function Pricing() {
  return (
    <Wrapper id="pricing">
      <div className="whiteBg">
        <div className="container">
          <HeaderInfo>
            <h1 className="font40 extraBold">The Variants Of Kavach</h1>
            <p className="font13">
            Kavach presents two extraordinary variants to cater to your hot and cold needs. Choose BlazeChill for a remarkable 
              <br />
            cooling experience that beats the heat, or upgrade to BlazeChill Pro for the ultimate pocket companion that brings
            <br/>
            the heat and chill. Stay in control of your comfort and ignite the coolness or warmth with Kavach BlazeChill and BlazeChill Pro.
            </p>
          </HeaderInfo>
          <TablesWrapper className="flexSpaceNull">
            {/* <TableBox>
              <PricingTable
                icon="roller"
                price="$29,99/mo"
                title="Starter"
                text="Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor."
                offers={[
                  { name: "Product Offer", cheked: true },
                  { name: "Offer", cheked: true },
                  { name: "Product Offer #2", cheked: false },
                  { name: "Product", cheked: false },
                  { name: "Product Offer", cheked: false },
                ]}
                action={() => alert("clicked")}
              />
            </TableBox> */}
            <TableBox>
              <PricingTable
                icon="affordable"
                price="₹600"
                title="Kavach FrostFire"
                text="Immerse yourself in the refreshing coolness of Kavach FrostFire. This variant delivers rapid full-body cooling to provide instant relief during scorching summers. With adjustable settings and a portable design, FrostFire keeps you comfortably chilled wherever you go. Stay cool, calm, and collected with Kavach FrostFire."
                // offers={[
                //   { name: "Product Offer", cheked: true },
                //   { name: "Offer", cheked: true },
                //   { name: "Product Offer #2", cheked: true },
                //   { name: "Product", cheked: true },
                //   { name: "Product Offer", cheked: false },
                // ]}
                action={() => alert("clicked")}
              />
            </TableBox>
            <TableBox>
              <PricingTable
                icon="affordable"
                price="₹1500"
                title="Kavach FrostFire Pro"
                text="Take control of your climate with the FrostFire Pro variant. Unleash the power of rapid full-body cooling in hot temperatures and indulge in cozy warmth during chilly winters. With advanced features like precise temperature control and extended battery life, FrostFire Pro sets the stage for year-round comfort. Embrace the Frost and Fire like never before with FrostFire Pro!"
                // offers={[
                //   { name: "Product Offer", cheked: true },
                //   { name: "Offer", cheked: true },
                //   { name: "Product Offer #2", cheked: true },
                //   { name: "Product", cheked: true },
                //   { name: "Product Offer", cheked: true },
                // ]}
                action={() => alert("clicked")}
              />
            </TableBox>
          </TablesWrapper>
        </div>
      </div>
      <div className="lightBg">
        <div className="container">
          <Advertising className="flexSpaceCenter">
            <AddLeft>
              <AddLeftInner>
                <ImgWrapper className="flexCenter">
                  <img className="radius8" src="https://i.postimg.cc/SsfXsGWx/kavach-app-25410ea88906c7dbdf91.png" alt="add" />
                </ImgWrapper>
              </AddLeftInner>
            </AddLeft>
            <AddRight>
              <h4 className="font15 semiBold">Kavach can be controlled with a mobile app</h4>
              <h2 className="font40 extraBold">Kavach App</h2>
              <p className="font12">
              Experience next-level convenience and customization with the Kavach mobile app, available for Android and iOS. Seamlessly control the temperature, adjust fan speed, monitor hydration levels, and check battery status, all from the palm of your hand. Take charge of your comfort anytime, anywhere, with the Kavach app, making your pocket AC truly personalized and effortlessly cool. Stay in control and enjoy a tailored cooling experience like never before!
              </p>
              <ButtonsRow className="flexNullCenter" style={{ margin: "30px 0" }}>
                <div style={{ width: "190px" }}>
                  <FullButton title="Pre Order" action={() => alert("clicked")} />
                </div>
                <div style={{ width: "190px", marginLeft: "15px" }}>
                  <FullButton title="Contact Us" action={() => { window.location.href="mailto:think.handels@gmail.com" }} border />
                </div>
              </ButtonsRow>
            </AddRight>
          </Advertising>
        </div>
      </div>
    </Wrapper>
  );
}

const Wrapper = styled.section`
  width: 100%;
  padding: 50px 0;
`;
const HeaderInfo = styled.div`
  margin-bottom: 50px;
  @media (max-width: 860px) {
    text-align: center;
  }
`;
const TablesWrapper = styled.div`
  @media (max-width: 860px) {
    flex-direction: column;
  }
`;
const TableBox = styled.div`
  width: 31%;
  @media (max-width: 860px) {
    width: 100%;
    max-width: 370px;
    margin: 0 auto
  }
`;
const Advertising = styled.div`
  padding: 100px 0;
  margin: 100px 0;
  position: relative;
  @media (max-width: 1160px) {
    padding: 60px 0 40px 0;
  }
  @media (max-width: 860px) {
    flex-direction: column;
    padding: 0 0 30px 0;
    margin: 80px 0 0px 0;
  }
`;
const ButtonsRow = styled.div`
  @media (max-width: 860px) {
    justify-content: space-between;
  }
`;
const AddLeft = styled.div`
  position: relative;
  width: 50%;
  p {
    max-width: 475px;
  }
  @media (max-width: 860px) {
    width: 80%;
    order: 2;
    text-align: center;
    h2 {
      line-height: 3rem;
      margin: 15px 0;
    }
    p {
      margin: 0 auto;
    }
  }
`;
const AddRight = styled.div`
  width: 50%;
  @media (max-width: 860px) {
    width: 80%;
    order: 2;
  }
`;
const AddLeftInner = styled.div`
  width: 100%;
  position: absolute;
  top: -300px;
  left: 0;
  @media (max-width: 1190px) {
    top: -250px;
  }
  @media (max-width: 920px) {
    top: -200px;
  }
  @media (max-width: 860px) {
    order: 1;
    position: relative;
    top: -60px;
    left: 0;
  }
`;
const ImgWrapper = styled.div`
  width: 100%;
  padding: 0 15%;
  img {
    width: 100%;
    height: auto;
  }
  @media (max-width: 400px) {
    padding: 0;
  }
`;




