import React from "react";
import styled from "styled-components";

export default function FullButton({ title, action, border }) {
  return (
    <Wrapper
      className="animate pointer radius8"
      // onClick={action ? () => action() : null}
      border={border}
    ><button data-tf-popup="LBVsS7qr" data-tf-opacity="100" data-tf-size="100" data-tf-iframe-props="title=Pre Order" data-tf-transitive-search-params data-tf-medium="snippet" style={{height: '100%', width: "100%"}}>
      {title}
    </button>
    </Wrapper>
  );
}

const Wrapper = styled.button`
  border: 1px solid ${(props) => (props.border ? "#707070" : "#7620ff")};
  background-color: ${(props) => (props.border ? "transparent" : "#7620ff")};
  width: 100%;
  padding: 15px;
  outline: none;
  color: ${(props) => (props.border ? "#707070" : "#fff")};
  :hover {
    background-color: ${(props) => (props.border ? "transparent" : "#580cd2")};
    border: 1px solid #7620ff;
    color: ${(props) => (props.border ? "#7620ff" : "#fff")};
  }
`;

