import React from "react";
import Slider from "react-slick";
import styled from "styled-components";

export default function ClientSlider() {
  const settings = {
    infinite: true,
    speed: 500,
    slidesToShow: 6,
    slidesToScroll: 2,
    arrows: false,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 4,
          slidesToScroll: 2,
        },
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
        },
      },
    ],
  };
  return (
    <div>
      <Slider {...settings}>
        <LogoWrapper className="flexCenter">
          <ImgStyle src="https://upload.wikimedia.org/wikipedia/en/a/a3/IIT_Kanpur_Logo.svg" alt="IIT Kanpur" />
        </LogoWrapper>
        <LogoWrapper className="flexCenter">
          <ImgStyle src="https://upload.wikimedia.org/wikipedia/en/1/1c/IIT_Kharagpur_Logo.svg" alt="IIT Kharagpur" />
        </LogoWrapper>
        <LogoWrapper className="flexCenter">
          <ImgStyle src="https://upload.wikimedia.org/wikipedia/commons/8/8e/Kaziranga_University_logo_tagline.svg" alt="Kaziranga University" />
        </LogoWrapper>
        <LogoWrapper className="flexCenter">
          <ImgStyle src="https://upload.wikimedia.org/wikipedia/commons/b/b0/Chandigarh_University_Seal.png" alt="Chandigarh University" />
        </LogoWrapper>
        <LogoWrapper className="flexCenter">
          <ImgStyle src="https://upload.wikimedia.org/wikipedia/en/a/a3/IIT_Kanpur_Logo.svg" alt="IIT Kanpur" />
        </LogoWrapper>
        <LogoWrapper className="flexCenter">
          <ImgStyle src="https://upload.wikimedia.org/wikipedia/en/1/1c/IIT_Kharagpur_Logo.svg" alt="IIT Kharagpur" />
        </LogoWrapper>
        <LogoWrapper className="flexCenter">
          <ImgStyle src="https://upload.wikimedia.org/wikipedia/commons/8/8e/Kaziranga_University_logo_tagline.svg" alt="Kaziranga University" />
        </LogoWrapper>
        <LogoWrapper className="flexCenter">
          <ImgStyle src="https://upload.wikimedia.org/wikipedia/commons/b/b0/Chandigarh_University_Seal.png" alt="Chandigarh University" />
        </LogoWrapper>
      </Slider>
    </div>
  );
}

const LogoWrapper = styled.div`
  width: 100%;
  height: 100px;
  cursor: pointer;
  :focus-visible {
    outline: none;
    border: 0px;
  }
`;
const ImgStyle = styled.img`
  width: 100%;
  height: 100%;
  padding: 10%;
`;