import React from "react";
import emailjs from "emailjs-com"
import Swal from "sweetalert2"
import styled from "styled-components";
// // Assets
// import ContactImg1 from "../../assets/img/contact-1_.png";
// import ContactImg2 from "../../assets/img/contact-2_.png";
// import ContactImg3 from "../../assets/img/contact-3_.png";

const SERVICE_ID = "service_lm6iu0n";
const TEMPLATE_ID = "template_6kugxlz";
const USER_ID = "ooBWioE7fLAjO-d2S";

export default function Contact() {
  const handleOnSubmit = (e) => {
    e.preventDefault();
    emailjs.sendForm(SERVICE_ID, TEMPLATE_ID, e.target, USER_ID)
      .then((result) => {
        console.log(result.text);
        Swal.fire({
          icon: 'success',
          title: 'Message Sent Successfully'
        })
      }, (error) => {
        console.log(error.text);
        Swal.fire({
          icon: 'error',
          title: 'Ooops, something went wrong',
          text: error.text,
        })
      });
    e.target.reset()
  };

  return (
    <Wrapper id="contact">
      <div className="lightBg">
        <div className="container">
          <HeaderInfo>
            <h1 className="font40 extraBold">Let's get in touch</h1>
            <p className="font13">
              Whether you have a question, feedback, or want to explore a partnership opportunity, we're here to 
              <br />
              listen and assist you. Fill out the form below, and our dedicated team will get back to
              <br />
              you promptly. We value your input and look forward to hearing from you!
            </p>
          </HeaderInfo>
          <div className="row" style={{ paddingBottom: "30px" }}>
            <div className="col-xs-12 col-sm-12 col-md-6 col-lg-6">
              <Form onSubmit={handleOnSubmit}>
                <label className="font13">First name:</label>
                <input type="text" id="fname" name="name" className="font20 extraBold" />
                <label className="font13">Email:</label>
                <input type="email" id="user_email" name="email" className="font20 extraBold" />
                <label className="font13">Subject:</label>
                <input id='mail_subject' type="text" name="subject" className="font20 extraBold" />
                <textarea id='mail_body' rows="4" cols="50" type="text" name="message" className="font20 extraBold" placeholder="Message" />
                <SumbitWrapper className="flex">
                  <ButtonInput type="submit" value="Send Message" className="pointer animate radius8" style={{color: 'black',maxWidth: "220px", paddingBottom: "3vh" }} />
                </SumbitWrapper>
              </Form>
            </div>
            <div className="col-xs-12 col-sm-12 col-md-6 col-lg-6 flex">
              <div style={{ width: "50%" }} className="flexNullCenter flexColumn">
                <ContactImgBox>
                  <img src="https://i.postimg.cc/j2qDJJXQ/contact-1-c334bf14f88b36cc01bf.png" alt="office" className="radius6" />
                </ContactImgBox>
                <ContactImgBox>
                  <img src="https://i.postimg.cc/nrdCKcS8/contact-2-e177a68c85670ed72b56.png" alt="office" className="radius6" />
                </ContactImgBox>
              </div>
              <div style={{ width: "50%" }}>
                <div style={{ marginTop: "100px" }}>
                  <img src="https://i.postimg.cc/rFmdVxzq/contact-3-573a00346ab0b368075d.png" alt="office" className="radius6" />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Wrapper>
  );
}

const Wrapper = styled.section`
  width: 100%;
`;
const HeaderInfo = styled.div`
  padding: 70px 0 30px 0;
  @media (max-width: 860px) {
    text-align: center;
  }
`;
const Form = styled.form`
  padding: 70px 0 30px 0;
  input,
  textarea {
    width: 100%;
    background-color: transparent;
    border: 0px;
    outline: none;
    box-shadow: none;
    border-bottom: 1px solid #707070;
    height: 30px;
    margin-bottom: 30px;
  }
  textarea {
    min-height: 100px;
  }
  @media (max-width: 860px) {
    padding: 30px 0;
  }
`;
const ButtonInput = styled.input`
  border: 1px solid #7620ff;
  background-color: #7620ff;
  width: 100%;
  padding: 15px;
  outline: none;
  color: #fff;
  :hover {
    background-color: #580cd2;
    border: 1px solid #7620ff;
    color: #fff;
  }
  @media (max-width: 991px) {
    margin: 0 auto;
  }
`;
const ContactImgBox = styled.div`
  max-width: 180px; 
  align-self: flex-end; 
  margin: 10px 30px 10px 0;
`;
const SumbitWrapper = styled.div`
  @media (max-width: 991px) {
    width: 100%;
    margin-bottom: 50px;
  }
`;









